<template>
  <v-row class="ma-sm-4">
    <v-col cols="12">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="dates"
        transition="scale-transition"
        offset-y
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="dateRangeText"
            solo
            flat
            hide-details="auto"
            background-color="#464646"
            label="Диапазон дат"
            readonly
            class="rounded-lg"
            v-on="on"
          >
            <template v-slot:prepend-inner>
              <v-icon class="mr-1">
                mdi-calendar
              </v-icon>
            </template>
          </v-text-field>
        </template>
        <v-date-picker
          v-model="dates"
          no-title
          scrollable
          range
        >
          <v-spacer />

          <v-btn
            text
            color="primary"
            @click="menu = false"
          >
            Отмена
          </v-btn>
          <v-btn
            text
            color="primary"
            @click="saveDates"
          >
            ОК
          </v-btn>
        </v-date-picker>
      </v-menu>
    </v-col>

    <v-col>
      <v-row>
        <v-col
          v-for="item of cards"
          :key="item.value"
          :cols="item.value === 'ngr' ? '12' : null"
        >
          <v-card
            flat
            class="px-3"
          >
            <v-card-title>{{ item.text }}</v-card-title>
            <v-card-text class="text-h3 white--text font-weight-bold">
              <span v-if="dashboardInfo">{{ dashboardInfo[item.value] }}</span>
              <span v-else>?</span>
              <span class="text-subtitle-2 font-weight-bold ml-1">RUB</span>
            </v-card-text>
            <template v-if="item.value === 'promotions'">
              <v-btn
                icon
                absolute
                top
                right
                @click="show = !show"
              >
                <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
              </v-btn>
              <v-expand-transition>
                <div v-show="show">
                  <v-divider />

                  <v-card-text>
                    <v-list-item
                      v-for="expense of expenses"
                      :key="expense.value"
                      two-line
                    >
                      <v-list-item-content>
                        <v-list-item-title>{{ expense.text }}</v-list-item-title>
                        <v-list-item-subtitle>
                          {{ dashboardInfo[expense.value] }}
                        </v-list-item-subtitle>
                      </v-list-item-content>
                    </v-list-item>
                  </v-card-text>
                </div>
              </v-expand-transition>
            </template>
          </v-card>
        </v-col>

        <v-col
          cols="12"
          class="text-center text-h5"
        >
          Игры
        </v-col>

        <v-col>
          <v-card
            flat
            class="px-4 pb-2"
          >
            <v-data-table
              :headers="headers"
              :items="[]"
              :items-per-page="5"
              no-data-text="Статистика по режимам отсутствует"
            />
          </v-card>
        </v-col>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      menu: false,
      dates: [],
      dateFrom: '',
      dateTo: '',
      cards: [
        {
          text: 'Поступления (депозиты)',
          value: 'deposits',
        },
        {
          text: 'Расходы (выводы)',
          value: 'withdraws',
        },
        {
          text: 'Иные расходы (по умолч. промо)',
          value: 'promotions',
        },
        {
          text: 'Доход (NGR)',
          value: 'ngr',
        },
      ],
      show: false,
      expenses: [
        {
          text: 'Бонусы',
          value: 'bonuses',
        },
        {
          text: 'Подарки',
          value: 'gifts',
        },
        {
          text: 'Рефералка',
          value: 'referrals',
        },
        {
          text: 'Комиссия платёжной системы',
          value: 'commissions',
        },
      ],
      dashboardInfo: {
        deposits: 13405,
        withdraws: 2345,
        promotions: 8344,
        ngr: 30498,
        bonuses: 8384,
        gifts: 1020,
        referrals: 1734,
        commissions: 3450,
      },
      headers: [
        { text: 'Режим', value: 'gameMode', class: 'white--text text-subtitle-2' },
        { text: 'Кол-во ставок:', value: 'betsQty', class: 'white--text text-subtitle-2' },
        { text: 'Сумма ставок:', value: 'betsAmount', class: 'white--text text-subtitle-2' },
        { text: 'Выиграно:', value: 'winsAmount', class: 'white--text text-subtitle-2' },
        { text: 'Проиграно:', value: 'looseAmount', class: 'white--text text-subtitle-2' },
        { text: 'GGR:', value: 'ggr', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
  },
  methods: {
    saveDates() {
      this.$refs.menu.save(this.dates);
      [this.dateFrom, this.dateTo] = this.dates;
    },
  },
};
</script>
